.SideDrawer{
    position: fixed;
    display: none;
    width:280px;
    max-width: 70%;;
    height: 100%;
    left:0;
    top:0;
    z-index: 200;
    background-color: var(--white-color);
    padding:32px 16px;
    transition: all 0.3  s ease-out;

    .content{
        
        .categoriesList{
            .category{
                margin: auto;
                padding: 2rem;

                i{
                    color:var(--secondary-color);
                    margin-right: 1rem;
                }

                span{
                    color:var(--grey-color);
                }
            }
        }
    }
}


@media (max-width:800px){
    .SideDrawer{
        display: block;
    }
}

.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);
}

