.overview{
    width: 80%;
    margin: auto;
    margin-top: 3rem;
    

    h2{
        color:var(--black-color);
        text-transform: uppercase;
        font-size: 20px;
    }

    span{
        color:var(--secondary-color);
    }

    .categoriesContainer{
        width: 100%;
        display: flex;
        margin: auto;
        justify-content: center;
        text-align: center;
        align-items: center;
        flex-wrap: wrap;

        i:first-of-type{
            font-size: 1.5rem;
            margin-right: 0.5rem;
            color:var(--primary-color);
            cursor: pointer;
        }
        i:last-of-type{
            font-size: 1.5rem;
            margin-left: .5rem;
            color:#dae9f0;
            cursor:not-allowed;
        }

        .category{
            width: 15.6%;
                span{
                    color:var(--black-color);
                    font-weight: 700;
                    font-size: 10;
                    text-align: center;
                    margin-left: 2rem;
                }
            .imgContainer{
                    background-color: var(--light-grey-color);
                    width: 80%;
                    height: 10rem;
                    margin: 2rem;
                    border-radius: 50%;
                    overflow: hidden;
                    display: flex;
                img{
                max-width: 90%;
                height: 70%;
                margin: auto;
            }
            }
            
        }
    }
    .iconsOverview{
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        width: 90%;
        margin: 4rem auto;
            .container{display: flex;
                        align-items: center;
                        justify-content: center;
                        
            }
        .iconContainer{
            background-color: var(--primary-color);
            border-radius: 50%;
            width: 3.5rem;
            height: 3.5rem;
            padding:.5rem;
            text-align: center;
            display: flex;
            align-items: center;
            margin-right:1rem ;
            i{
                color:var(--white-color);
                font-size: 1.3rem;
                margin: auto;
            }
            

        }

        .description{
            margin-bottom: 1rem;
                h4{
                    color:var(--black-color);
                    text-transform: uppercase;
                    margin-bottom: 5px;
                }

                span{
                    color:var(--grey-color);
                    font-size: 10px;
                    
                }
        }
    }
}

@media screen and (max-width:1000px){
    .overview{
        h2{
            text-align: center;
        }
        span{
            margin-left: 4rem;
        }
        
        
        .categoriesContainer{
            i{
                display: none;
            }
        .category{
            width: 40%;
            margin-top: 2rem;
        }
    }
    .iconsOverview{
        .container{
            width: 50%;
        }
        .iconContainer{
            margin: 0 1rem;

        }
        .description{
            margin-bottom: 1rem;
            h4{
                text-align: center;
                font-size: 15px;
            }
            span{
                margin: auto;
                padding: .5rem;
            }
        }
    }
    }

    

}
@media screen and (max-width:500px){
    .overview{
        span{
            font-size: 14px;
            margin-left: 1.5rem;
        }
    }
}